import { Base64Tool } from "../tools/Base64Tool"
	/**
	 * ...
	 * @author ww
	 */
	export class DivScripts 
	{
		
		constructor(){
			
		}
		 static data:string = "";
		static init():void
		{
			var script:string;
			script = Base64Tool.decodeToByte(DivScripts.data).readUTFBytes();
			window["eval"](script);
		}
	}


